@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;400;600;700;900&family=Poppins:wght@300;400;500;600&display=swap);
header {
    height: 100vh;
    padding-top: 12rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ********* CTA ********** */
.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* ********* ME ********** */
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 20rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 10rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 2.5rem 0rem 1.5rem 1.5rem;
}

.me__image {
    width: 200px;
    -webkit-filter: brightness(67%);
            filter: brightness(67%);
}

/* ********* MEDIA QUERIES (MEDIUM DEVICES) ********** */
@media screen and (max-width: 1024px){
    header {
        height: 68vh;
    }
}

/* ********* MEDIA QUERIES (SMALL DEVICES) ********** */
@media screen and (max-width: 600px){
    header {
        height: 100vh;
    }
}

@media screen and (max-width: 400px){
    header {
        height: 100vh;
    }
}

.logo {
    width: 100px;
}

.btn-logo {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: var(--color-white);
    padding: 1.2rem 1.2rem 1rem 1.2rem;
    border-radius: 0.4rem;
    align-items: center;
    cursor: pointer;
    border: 1px solid var(--color-white);
    transition: var(--transition);
}

.btn-logo:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-image {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    background: var(--color-white);
    padding: 1rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-bg);
    transition: var(--transition);
}

.btn-image:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

/* ********* MEDIA QUERIES (MEDIUM DEVICES) ********** */
@media screen and (max-width: 1024px){
    .btn-image {
        padding: 1rem 1.2rem;
    }
}

/* ********* MEDIA QUERIES (SMALL DEVICES) ********** */
@media screen and (max-width: 600px){
    .btn-image {
        padding: 1rem 1.2rem;
    }

    .logo {
        margin-top: 0.5rem;
    }
}

@media screen and (max-width: 400px){
    .btn-image {
        padding: 1rem 1.2rem;
    }
}
nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent; 
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-white);
    color: var(--color-bg);
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-white), transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transition: var(--transition);
}

.about__me-image:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.about__me-image-me {
    -webkit-filter: brightness(70%);
            filter: brightness(70%);
}

.about__content p {
    margin: 2rem 0 1.6rem;
    color: var(--color-light);
}

/* ********* MEDIA QUERIES (MEDIUM DEVICES) ********** */
@media screen and (max-width: 1024px){
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

/* ********* MEDIA QUERIES (SMALL DEVICES) ********** */
@media screen and (max-width: 600px){
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }

    .about__me {
        padding: 10px;
    }
}
.portfolio__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5rem;
    gap: 5rem;
}

.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover {
    border-color: var(--color-bg-variant);
    background: transparent;
}

.portfolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta {
    display: absolute;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* ********* MEDIA QUERIES (MEDIUM DEVICES) ********** */
@media screen and (max-width: 1024px){
    .portfolio__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

/* ********* MEDIA QUERIES (SMALL DEVICES) ********** */
@media screen and (max-width: 600px){
    .portfolio__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 45% 45%;
    grid-gap: 12rem;
    gap: 12rem;
    justify-content: center;
    margin-bottom: 25rem;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
    color: var(--color-white);
}

.contact__option a:hover {
    color: var(--color-primary-variant);
}

/* ********* MEDIA QUERIES (MEDIUM DEVICES) ********** */
@media screen and (max-width: 1024px){
    .container.contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
        margin-bottom: 15rem;
    }
}

/* ********* MEDIA QUERIES (SMALL DEVICES) ********** */
@media screen and (max-width: 600px){
    .container.contact__container {
        width: var(--container-width-sm);
        margin-bottom: 8rem;
    }
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;  
}

:root {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(data:image/png;base64,);
}

/* ********** GENERAL STYLES ************** */
.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

section > h2 {
    color: #fff;
    color: var(--color-white);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

a {
    color: #1f1f38;
    color: var(--color-bg);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #fff;
    color: var(--color-white);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #1f1f38;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    border-color: transparent;
}

.btn-primary {
    background: #fff;
    background: var(--color-white);
    color: #1f1f38;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ********* MEDIA QUERIES (MEDIUM DEVICES) ********** */
%media screen and (max-width: 1024px){
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* ********* MEDIA QUERIES (SMALL DEVICES) ********** */
%media screen and (max-width: 600px){
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}
